.content-box {
    vertical-align: middle;
    border: .1vh solid #222A37;
    border-radius: .75vh;
    cursor: pointer;
    width: 25vw;
    height: 50vh;
}

.background-image {
    z-index: -1;
    position: relative;
    width: 100%;
    height: 99.5%;
    border: .1vh solid #222A37;
    border-radius: .75vh;
    outline: none;
    opacity: .75;
    object-fit: cover;
}

.content-box p {
    display: none;
    position: absolute;
    bottom: 1vh;
    width: 100%;
    text-align: center;
    font-style: italic;
    color: grey;
} 

.content-box:hover .background-image{
    animation-fill-mode: forwards;
    display: none;
}

.content-box:hover {
    transform: scale(1.01); 
    animation: content_pulse 2s infinite;
}

.content-box:hover .title-div{
    display: none; 
}

.content-box:hover p {
    display: block;
}  

.content-box:hover .endzone-sub-header {
    display: block;
    z-index: -1;
}



.title-div {
    position: absolute;
    display: inline-flex;
    justify-content: center;
    text-align: center;
    margin: auto;
    color: white;
    border-radius: .75vh;
    border-top: solid .1vh #0984e3;
    border-bottom: solid .1vh #0984e3;
    width: 25.3%;   
    background-color: rgba(34, 42, 55, .2);
    font-weight: 100;
}


h3 {
    font-size: 3.5vh;
    font-weight: 100;
}

.endzone-sub-header {
    position: absolute;
    display: none;
    text-align: center;
    font-size: 3vh;
    color: white;
    padding: 2vh;
    font-weight: 100;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}

@media screen and  (max-width: 600px){
    .content-box {
        position: relative;
        width: auto;
        margin: auto;
        margin-top: 1vh;
        margin-bottom: 1vh;
        margin-left: 5vw;
        margin-right: 5vw;
    }
    .title-div {
        position: absolute;
        margin-top: 1vh;
        margin-left: 4vw;
        width: 80vw;    
    }

    h3 {
        font-size: 4vh;
    }

    .endzone-sub-header {
        position: absolute;
        display: block;
        bottom: 0;
        margin: top;
        background-color: rgba(34, 42, 55, .5);
    }

    .content-box:hover {
        transform: none; 
        animation: none;
    }

    .content-box:hover p {
        display: none;
    }  
    
    .content-box:hover .endzone-sub-header {
        display: block;
        z-index: 0;
    }

    .content-box:hover .title-div{
        display: block; 
    }

    .content-box:hover .background-image{
        animation-fill-mode: none;
        display: block;
    }
}