@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
  margin: 0;
	font-family: 'Audiowide';
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.top-nav {
  height: auto;
  width: 100vw;
  display: flex;
  background-color: #0d1019;
  
}

.top-nav-custom-border {
  width: 100%;
  border-bottom: .5vh solid #353b48;
  transform: scale(1);
	animation: pulse 3s infinite;
}

@keyframes pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);

	}

	70% {
		box-shadow: 0 0 0 .5vh rgba(0, 0, 0, 0);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    border-bottom: .5vh solid #0984e3;
	}
}

.top-nav ul li{
  left: 0;
  list-style: none;
  display: inline-block;
  justify-content: space-evenly;
  margin-right: 2vw;
  margin-top: 1.5vh;
}

.top-nav a{
  text-decoration: none;
  font-size: 2.25vh;
  padding-bottom: .5vh;

}

.standard-link {
  color: white;
  border-bottom: solid .1vh #0984e3;
}

.active-link {
  color: #0984e3;
  border-bottom: solid .1vh white;
}

.top-nav a:hover {
  color: #0984e3;
  transition: all .3s ease-in-out;
  border-bottom: solid .1vh white;
}

.log-in button {
  font-family: 'Audiowide';
  font-weight: 500;
  background-color: #222A37;
  color: white;
  border: 0;
  border-radius: .75vh;
  font-size: 2vh;
  padding: 1vh 1vw;
  cursor: pointer;
  min-height: 4vh;
}

.top-nav button:hover {
  transform: scale(1.01); 
  animation: content_pulse 2s infinite;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}

.top-nav button:hover {
  background-color: #0984e3; 
  transition: all .3s ease-in-out;
  transform: scale(1.1);
}

.top-nav .log-in {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  height: 9vh;
  width: 10%;
}

.logo-container {
  margin: 1vh;
  width: 15%;
  height: auto;
}

.logo-container .logo {
  width: 100%;
  height: 100%;
  padding: 0,0,0;
}

.logo:hover {
  cursor: pointer;
}


@media screen and  (max-width: 800px){
  .logo-container {
      display: block;
      width: 33%;
      margin: auto;
      margin-top: 1vh;
      margin-bottom: 1vh;
  }

  .top-nav {
    display: inline-block;
  }

  .top-nav ul {
    text-align: center;
    width: 90%;
    margin: auto;
    padding: 0;
  }

  .top-nav .log-in {
    width: 100%;
  }

  .log-in button {
    width: 90%;
    margin: 5%;
    height: 15%;
    text-align: center;
    padding: .5vh;
  }

  .top-nav button:hover {
    background-color: #0984e3; 
    transition: none;
    transform: none;
  }

  .mobile-spacer-div {
    display: block;
  }
}