@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
    overflow-y: scroll;
}

.options {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100vw;
    height: 90vh;
}

@media (max-width: 600px) {
    .options {
        display: inline-block;
    }

}